import { Config } from './models/config';
import { unzoomImage, getScale, sumValues } from './utils/helpers';
import { Inject, Injectable, ElementRef } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { css } from './styles';
import { debounce } from './utils/debounce';

@Injectable({
  providedIn: 'root',
})
export class NgZoom {
  private window: Window;
  constructor(@Inject(DOCUMENT) private document: Document) {
    this.window = this.document.defaultView as Window;
    this.injectStyles();

    this.document.body.addEventListener('click', () => {
      this.handleClick(null);
    });
    this.document.addEventListener('keydown', this.handleKeydown);
    this.window.addEventListener('scroll', this.handleUnzoomingInteraction);
    this.window.addEventListener('resize', this.handleUnzoomingInteraction);
  }

  public listen(imageRef: ElementRef<HTMLImageElement>): void {
    const image: HTMLImageElement = imageRef.nativeElement;
    this.processImage(image);
    image.addEventListener('click', () => {
      this.handleClick(image);
    });
  }

  private defaultConfig: Config = {
    padding: 20,
    exceed: false,
  };

  private zoomed: HTMLImageElement | null = null;

  private handleClick = debounce(
    (image: HTMLImageElement | null) => {
      if (this.zoomed) {
        unzoomImage(this.zoomed);
        this.zoomed = null;
        return;
      }

      if (image) {
        this.zoomImage(image, this.defaultConfig);
        this.zoomed = image;
      }
    },
    500,
    {
      isImmediate: true,
    }
  );

  private handleUnzoomingInteraction = (): void => {
    if (!this.zoomed) return;
    unzoomImage(this.zoomed);
    this.zoomed = null;
  };

  private handleKeydown = (e: KeyboardEvent): void => {
    if (e.code != 'Escape') return;
    e.preventDefault();
    if (this.zoomed) {
      unzoomImage(this.zoomed);
      this.zoomed = null;
    }
  };

  private injectStyles = (): void => {
    const styles: HTMLStyleElement = this.document.createElement('style');
    styles.innerHTML = css;

    this.document.head.appendChild(styles);
  };

  private zoomImage = (image: HTMLImageElement, config: Config): void => {
    const imageRect: DOMRect = image.getBoundingClientRect();
    const imageStyle: CSSStyleDeclaration = this.window.getComputedStyle(image);

    const imageWidth: number =
      imageRect.width -
      sumValues(imageStyle, [
        'borderLeftWidth',
        'borderRightWidth',
        'paddingLeft',
        'paddingRight',
      ]);

    const imageHeight: number =
      imageRect.height -
      sumValues(imageStyle, [
        'borderTopWidth',
        'borderBottomWidth',
        'paddingTop',
        'paddingBottom',
      ]);

    const vw: number = this.window.innerWidth;
    const vh: number = this.window.innerHeight;

    const shouldExceed: boolean =
      config.exceed || image.dataset?.['imageZoomExceed'] == 'true';
    let scale: number = getScale(imageHeight, imageWidth, vh, vw);

    if (!shouldExceed) {
      const limitedScale: number = getScale(
        imageHeight,
        imageWidth,
        image.naturalHeight,
        image.naturalWidth
      );
      scale = Math.min(scale, limitedScale);
    }

    const isPaddingNeeded: boolean =
      config.padding >
      Math.min(vh - imageHeight * scale, vw - imageWidth * scale) / 2;

    if (isPaddingNeeded) {
      let scaleWithPaddingBeforeExceed: number = getScale(
        imageHeight + config.padding,
        imageWidth + config.padding,
        vh,
        vw
      );

      if (!shouldExceed) {
        const limitedScale: number = getScale(
          imageHeight,
          imageWidth,
          image.naturalHeight - config.padding,
          image.naturalWidth - config.padding
        );
        scaleWithPaddingBeforeExceed = Math.min(
          scaleWithPaddingBeforeExceed,
          limitedScale
        );
      }

      scale = scaleWithPaddingBeforeExceed;
    }

    const doc: HTMLElement = this.document.documentElement;
    const scrollLeft: number =
      (this.window.pageXOffset || doc.scrollLeft) - (doc.clientLeft || 0);
    const scrollTop: number =
      (this.window.pageYOffset || doc.scrollTop) - (doc.clientTop || 0);

    const imageCenterX: number =
      scrollLeft + imageRect.left + imageRect.width / 2;
    const imageCenterY: number =
      scrollTop + imageRect.top + imageRect.height / 2;

    const screenCenterX: number = scrollLeft + vw / 2;
    const screenCenterY: number = scrollTop + vh / 2;

    const translateX: number = (screenCenterX - imageCenterX) / scale;
    const translateY: number = (screenCenterY - imageCenterY) / scale;

    image.classList.add('image-zoom-zoomed');
    image.parentElement?.classList.add('image-zoom-wrapper-zoomed');
    image.style.transform = `scale(${scale}) translate(${translateX}px, ${translateY}px)`;
  };

  private processImage = (image: HTMLImageElement) => {
    // create an image wrapper element
    const wrapper = this.document.createElement('div');

    // let wrapper mimick pearl display property to not break anything
    wrapper.classList.add('image-zoom-wrapper');
    wrapper.style.display = this.window.getComputedStyle(image).display;
    image.parentElement?.insertBefore(wrapper, image);
    wrapper.appendChild(image);

    image.classList.add('image-zoom');
    image.style.transform = 'scale(1)';
  };
}
