export const sumValues = (
  source: CSSStyleDeclaration,
  keys: string[]
): number => {
  let result: number = 0;

  keys.forEach((key: string) => {
    const property: string = source[key];
    const value: number = parseInt(property, 10) || 0;
    result += value;
  });

  return result;
};

export const getScale = (
  imageHeight: number,
  imageWidth: number,
  maxHeight: number,
  maxWidth: number
): number => {
  const widthScale: number = maxWidth / imageWidth;
  const heightScale: number = maxHeight / imageHeight;
  const widthScaleIsOkay: boolean = imageHeight * widthScale <= maxHeight;
  return widthScaleIsOkay ? widthScale : heightScale;
};

export const unzoomImage = (image: HTMLImageElement) => {
  image.style.transform = 'scale(1)';
  image.parentElement?.classList.remove('image-zoom-wrapper-zoomed');
  image.addEventListener(
    'transitionend',
    () => {
      image.classList.remove('image-zoom-zoomed');
    },
    {
      once: true,
    }
  );
};
